import axios from 'axios'
// import router from '../../router'
import { v4 as uuidv4 } from 'uuid'
import moment from 'moment'
import addIdResult from '../../utils/addIdResult'

const state = {
    job_results: {
        live_result: [],
        interested: [],
        applied: [],
        invited_offer: []
    },
    selected_job: {
        title: "",
        location: "",
        job_type: "",
        salary: ""
    },
    curResult: { tabName: 'live_result', index: 0 },
    subTabs: ['live_result', 'interested', 'applied','invited_offer']
}

const getters = {
    getResults: (state) => state.job_results,
    getCurTab: state => state.curResult,
    getSelectedJob: state => state.selected_job
}

const actions = {
    async searchJobBulk({ commit, state }, query) {
        commit('setLoading', true)
        let { title, location = '', page = 1 } = query

        commit('setResults', {
            ...state.job_results, live_result: []
        })

        try {
            const response = await axios.get(`/api/profile/search?title=${title}&location=${location}&page=${page}`)

            let live_result = addIdResult(response.data)
            commit('setResults', { ...state.job_results, live_result })
            // router.push({ query })
            commit('setLoading', false)
            return true
        } catch (err) {
            console.log("Bulk fetch error: ", err)
            commit('setLoading', false)
            return false
        }
    },
    resetLive({ commit, state }) {
        commit('setResults', {
            ...state.job_results, live_result: []
        })
    },
    async getSingleJob(_, value) {

        try {
            let result = await axios.get(`/api/profile/single-job${value}`)
            result.data.animate = true
            let currentTime = Date.now()
 
            return { ...result.data, 
                id: uuidv4(), current_tab: '', 
                desc_expand: false, posted: currentTime, time: moment(currentTime).fromNow()
            }
            
        } catch (error) {
            console.log(error)
            
            return false
        }  
    },
    removeSection({ commit, state }, card) {
        let { tabName, index } = state.curResult

       

        commit('setResults', {
            ...state.job_results,
            [tabName]: state.job_results[tabName].filter(item => item.id !== card.id)
        })

        if(index !== 0) {
            commit('setLoading', true)
            axios.post('/api/jobs/remove-job', { card, remove_type: tabName })
                .then(result => {
                    commit('setResults', {
                        ...state.job_results,
                        applied: addIdResult(result.data.data.applied, true),
                        interested: addIdResult(result.data.data.interested, true),
                        invited_offer: addIdResult(result.data.data.invited_offer, true)
                    })
                    commit('setLoading', false)
                })
                .catch(err => {
                    console.log(err)
                    commit('setLoading', false)
                })
        }
    },
    insertSection({ commit, state}, { card, operationKey }) {
       

        let { tabName, index } = state.curResult

       

        try {
            let pre_record = state.job_results[tabName]
                .filter(item => item.id !== card.id)

            let new_insertKey = state.subTabs[index+operationKey]
            let new_insertSet = state.job_results[new_insertKey]

          
            
            new_insertSet.unshift(card)

            commit('setResults', {
                ...state.job_results,
                [tabName]: pre_record,
                [new_insertKey]: new_insertSet
            })

            // api call make a successful save data
            commit('setLoading', true)
            axios.post('/api/jobs/insert-job', { card, add_type: new_insertKey, currentIndex: index })
                .then(res => {
                    
                    commit('setResults', {
                        ...state.job_results,
                        applied: addIdResult(res.data.data.applied, true),
                        interested: addIdResult(res.data.data.interested, true),
                        invited_offer: addIdResult(res.data.data.invited_offer, true)
                    })
                    commit('setLoading', false)
                })
                .catch(err => {
                    console.log(err)
                    commit('setLoading', false)
                })

        } catch (err) {
            console.log("Err: ", err)
        }
    }
}

const mutations = {
    setResults: (state, value) => state.job_results = value,
    setCurSubTab: (state, value) => state.curResult = value,
    setSelectedJob: (state, value) => state.selected_job = value
}

export default {
    state,
    getters,
    actions,
    mutations
}