import { v4 as uuidv4 } from 'uuid'
import moment from 'moment'

function addIdResult(arr, sort) {
    let mappedArr = arr.map(value => {
        let time 

        if(value.posted.match(/^[0-9]*$/)) {
            time = moment(parseInt(value.posted)).fromNow()
        } else {
            time = value.posted
        }

        return { ...value, id: uuidv4(), current_tab: '', desc_expand: false, time  }
    })

    if(sort) {
        return mappedArr.reverse()
    } else {
        return mappedArr
    }
}

export default addIdResult