<template>
    <div class="search-container d-flex">

      <div class="search-bar-p d-md-flex d-lg-flex d-xl-flex align-end">
        
        <div class='search-bar d-flex pa-3 align-center mr-md-5 mr-lg-5 mr-xl-5 relative'>

          <img style="height: 1.3rem; width: auto;" v-if="getMode == 0"
            src="../assets/new_file01/jobs.png" 
            class="ml-1" @click="changl()" />
          
          <img style="height: 1.3rem; width: auto;" v-if="getMode == 1"
            src="../assets/new_file01/freelancer.png" 
            class="ml-1" @click="changl()" />

          <div class="v-bar-sm mx-2"></div>

          <!-- <input class="search-text" :placeholder="search[activeIndex].placeHolder" v-model="search[activeIndex].text" /> -->
          <div style="position: relative">
            <div class="d-flex align-center" style="position: relative">
              
              <input class="search-text pt-1"
                ref="ctinput"
                :placeholder="search[activeIndex].placeHolder" 
                @focus="setFocus(true)"
                @blur="setFocus(false)"
                v-model="search[activeIndex].text" />
              
              <img 
                style="height: 1.4rem; width: auto; position: absolute; right: -1%" 
                src="../assets/new_files/preload.gif" class="ml-1" v-if="search[4].text.length >= 1 && preload && isFocus" />
          
            </div>
            
            
            <transition name='anime' type="animation">           
              <div v-if="search[activeIndex].text.length >= 1 && suggest.length >= 1 && isFocus && preIsLoad">
                  <div class="pretext-p-cover"></div>
                  <div class="pb-4 pretext-p" >

                  <div v-for="(item, index) in suggest" 
                    @click="search[activeIndex].text=item"
                    :key="index" class="pretext-item px-4 py-1">{{item}}</div>
                </div>
              </div>
            </transition>

            
          </div>

          <div class="v-bar-sm mx-3"></div>
          
          <!-- Large screen -->
          <div class="mt-2" >
            <v-tooltip nudge-top="-8" color="#EDF2FF" allow-overflow dark content-class="tooltip"
              bottom v-for="(item, index) in search" :key="index">
              <template v-slot:activator="{ on }">
                
                <img class="search_tag" 
                  @click="showDrop(item._id)"
                  @mousemove="search[item._id].hover=true"
                  @mouseleave="search[item._id].hover=false"  
                  v-on="on"
                  :class="[
                    index != 5 ? 'mr-2' : '',
                    item._id != activeIndex ? 'grow' : ''
                  ]"
                  :src="[
                  item.hover || item._id == activeIndex ? 
                  item.activeImg : item.inactiveImg]" 
                />

              </template>
              <span>{{item.id}}</span>
            </v-tooltip>
          </div>

          
          <!-- <div class="hidden-md-and-up">
            <div style="cursor: pointer;">
              
              
              <div style="position: relative">
                <div class="d-flex align-center" @click="showSmallInput=!showSmallInput">
                  <img :src="search[activeIndex].activeImg" class='search_tag-active mr-1' />
                  <img :src="dropimg" alt="dropdown" class="drop-style" />
                </div> 
              

                <transition name='anime' type="animation">  

                  <div style="position: absolute" class="d-flex flex-column mt-3" v-if="showSmallInput">                 
                    <img class="search_tag-sm"
                      v-for="(item, index) in search_sm" :key="index"
                      :class="[
                        item._id != activeIndex ? 'grow' : '',
                        index != 5 ? 'mb-2' : ''
                      ]"
                      @click="showDrop(item._id)"
                      @mousemove="hoverActive(item._id)"
                      @mouseleave="outDeactive(item._id)"

                      :src="[item.hover ? item.activeImg : item.inactiveImg]" 
                    />                  
                  </div>     

                </transition>  
              </div>
            </div> 
          </div> -->
          
        </div>
        

        <button          
          class="primary search-btn px-8 py-3 font-weight-bold hidden-sm-and-down" 
          style="font-size: 0.8rem" @click="searchJob()">
          <div class="d-flex align-center">
            <!-- <img :src="searchImg" alt="load" class="mr-2"
              style="height: 1.2rem; width: auto;" 
              v-if="getLoading && realtime"
            > -->
            <span >
              SEARCH</span>
          </div>


        </button>

        <!-- <div class="text-center mt-6 hidden-md-and-up">
          <button 
            class="primary search-btn px-8 py-3 font-weight-bold" 
            style="font-size: 0.8rem; margin: 0 auto;" @click="searchJob()">
            
            <div class="d-flex align-center">
              <img :src="searchImg" alt="load" class="mr-2"
                style="height: 1.2rem; width: auto;"
                v-if="getLoading && realtime"
              >
              <span>
                SEARCH one</span>
            </div>
          </button>
        </div> -->
      </div>

    </div>
</template>

<script>
import axios from 'axios'
import router from '../router'
import { mapActions, mapGetters } from 'vuex'

//images import 
import skillsImg from '../assets/new_files/skills.png'
import skillsImga from '../assets/new_files/dark/skills.png'
import salaryImg from '../assets/new_files/payment.png'
import salaryImga from '../assets/new_files/dark/payment.png'
import timeImg from '../assets/new_files/time.png'
import timeImga from '../assets/new_files/dark/time.png'
import remoteImg from '../assets/new_files/remote.png'
import remoteImga from '../assets/new_files/dark/remote.png'
import locationImg from '../assets/new_files/location.png'
import locationImga from '../assets/new_files/dark/location.png'
import titleImg from '../assets/new_files/title.png'
import titleImga from '../assets/new_files/dark/title.png'

import searchImg from '../assets/new_files/rolling.gif'
import dropdw from '../assets/new_files/chevron-down.png'
import queryBuilder from '../utils/queryBuilder'

import _debounce from 'lodash.debounce'

//pretext
import {
  jobTitles,
  jobTypes,
  skills,
  salary,
  place
} from '../utils/pretext'

export default {
    name: 'SearchBar',
    props: ['getquery','realtime'],
    data: () => ({
        search: [
           { id: 'Skills', text: '', placeHolder: 'Skills', activeImg: skillsImga, inactiveImg: skillsImg, _id: 0, hover: false },
           { id: 'Salary', text: '', placeHolder: 'Salary', activeImg: salaryImga, inactiveImg: salaryImg, _id: 1, hover: false },
           { id: 'Job Type', text: '', placeHolder: 'Job type', activeImg: timeImga, inactiveImg: timeImg, _id: 2, hover: false},
           { id: 'Remote', text: '', placeHolder: 'Remote', activeImg: remoteImga, inactiveImg: remoteImg, _id: 3, hover: false },
           { id: 'Location', text: '', placeHolder: 'Location', activeImg: locationImga, inactiveImg: locationImg, _id: 4, hover: false },
           { id: 'Job Title', text: '', placeHolder: 'Job title', activeImg: titleImga, inactiveImg: titleImg, _id: 5, hover: false },
        ],
        activeIndex: 5,
        isFocus: false,
        preload: false,
        dropimg: dropdw,
        searchImg: searchImg,
        isHoverTab: false,
        showSmallInput: false,
        search_img: [
          '../assets/new_file01/jobs.png',
          '../assets/new_file01/freelancer.png'
        ]
    }),
    
    asyncComputed: {
     suggest() {  
          let pres = [skills,salary,jobTypes,['Remote'],place,jobTitles]         
           let values = []
           if(this.activeIndex == 4) {

             this.preload = true
             return axios.post('/api/users/place', { data: this.search[this.activeIndex].text.toLowerCase() })
              .then(result => {
                this.preload = false
                return result.data.sort((a, b) => a.length - b.length).slice(0, 5)
              })
              .catch(err => {
                console.log(err)
                this.preload = false
                return []
              })

           } else {
              pres[this.activeIndex].forEach(element => {       
                let lowword = element.toLowerCase()        
                let b = lowword.indexOf(this.search[this.activeIndex].text.toLowerCase())

                if(b != -1) { values.push(element) }             
              })

              return values.sort((a, b) => a.length - b.length).slice(0, 5)
         }
      },
      preIsLoad() {
        if(this.activeIndex == 4) {
          return !this.preload
        } else {
          return true
        }
      }
    },
    methods: {
      ...mapActions(['searchJobBulk']),
      debouncedOnChange () {
          return _debounce(this.onNoteChange, 3000);
      },
      setFocus(value) {
        setTimeout(() => {
          this.isFocus = value
        }, 50)
      },
      showDrop(index) {
        
        setTimeout(() => {
            this.showSmallInput = false
            this.activeIndex = index
            this.search[index].hover=false
            if (this.search[index].text.length < 1) {
                this.$refs.ctinput.focus()
            }
        }, 300) //change if required
      },
      async searchJob() {
        let queryData = {
          skills: this.search[0].text,
          salary: this.search[1].text,
          jobType: this.search[2].text,
          remote: this.search[3].text,
          location: this.search[4].text,
          title: this.search[5].text
        }

        let queryUrl = queryBuilder(queryData)

         if(queryData.title.length > 2) {
           
          router.push({ query: queryUrl })
          
          this.$store.commit('setLoading', true)
          this.$store.commit('setSelectedJob', queryUrl)
          if(this.realtime) {
            await this.searchJobBulk(queryUrl)
            router.push({ query : { ...queryUrl } }).catch()
          } else {
            this.$store.commit('setSelectedJob',this.$route.query)
            let operation = await this.searchJobBulk(queryUrl)
            if(operation) {
              router.push({ name: 'Live_results', query : { ...queryUrl } }).catch()
            }
          }

          this.$store.commit('setLoading', false)
         }
      },
      findJobs() {
        if(this.search[5].text.length > 1 &&
        this.search[4].text.length > 1) {

        this.$router.replace({ query: {...this.getQuery, page: Math.floor(Math.random() * 2) + 1}})
        }
      }
    },
    computed: {
      ...mapGetters(['getLoading', 'getMode']),
      search_sm() {    
        return this.search.filter(el => el._id !== this.activeIndex)
      }
    },
    mounted() {
      if (!this.$route.query.title || 
          !this.$route.query.location || 
          !typeof this.$route.query.page == 'number') {
            // router.push('/')

            return 
          }


      if(this.getquery) {
        let { title, location } = this.$route.query
        
        setTimeout(() => {
          this.search[5].text = title
          this.search[4].text = location
        }, 150)
        
      }
    }
}
</script>

<style scoped>
  .search-container {
    position: relative;
  }

  .search-bar-p {
      margin: 0 auto;
  }

  .search-bar {
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.06);
    
    height: 2.8rem;
    border-radius: 2rem;
  }

  .search_tag {
     height: 1.9rem !important;
     width: auto !important;
     cursor: pointer;
  }

  .search_tag-sm {
     height: 1.9rem !important;
     width: 1.9rem !important;
     cursor: pointer;
  }

  .drop-style {
    height: 1.2rem;
    opacity: 0.7;
    width: auto;
    /* transform: translateY(5.7rem); */
  }

  .search_tag-active{
     height: 1.8rem !important;
     width: 1.8rem !important;
     cursor: pointer;
     /* transform: translateY(5.95rem); */
     padding-right: -1px;
  }

  .grow { transition: all .2s ease-in-out; }
  .grow:hover { transform: scale(1.2) }
  .grow:active { transform: scale(0.9) }
   
   .sm-expand {
     margin-top: 12.8rem;
   }

   .sm-shrink {
     margin-top: 12.8rem;
   }
  .hide-it {
     display: none !important;
  }

  .v-bar-sm {
      height: 1.7rem;
      width: 1px;
      background-color: #E7E7E7; 
  }

  .search-text {
    flex: 1;
    color: #757575;
    font-size: 0.9rem;
    outline: none;
  }

  .search-text::placeholder {
    color: #aaaaaa;
  }

  .pretext-p {
    width: 12.8rem;
    position: absolute;
    transform: translateX(-0.8rem) translateY(1.7rem) ;
    border-bottom-left-radius: 1.2rem;
    border-bottom-right-radius: 1.2rem;
    background-color: white;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.07);
  }

  .pretext-p-cover {
    width: 12.8rem;
    height: 1.5rem;
    position: absolute;
    transform: translateX(-0.8rem) translateY(0.3rem) ;
    background-color: white;
    z-index: 10;
  }

  @media only screen and (min-width: 600px) {
    .search-text {
      width: 15rem;
    }

    .pretext-p {
      width: 16.6rem; 
    }

    .pretext-p-cover {
      width: 16.6rem; 
    }
   }

  .pretext-item {
    color: #949494;
    font-size: 0.88rem;
    cursor: pointer;
  }

  .pretext-item:hover {
    color: white;
    background-color: #0046FE;
    font-size: 0.88rem;
    cursor: pointer;
    
  }

  .tooltip {
    /* font-weight: 700; */
    color: #89AAFF;
    font-size: 0.8rem;
  }

  .search-btn {
      border-radius: 8rem;
      color: white;
      font-size: 0.9rem;  
  }

  .p-range {
    height: 0.8rem;
  }

  .p-tag {
    font-size: 10px;
    color: white;
    border-radius: 0.2rem;
  }

  .p-from {
    background-color: blue;
    height: 0.8rem;
    font-size: .8rem;
  }

/* animation style */
.anime-enter {
    opacity: 0;
  }

.anime-enter-active {
    animation: slide-in 0.3s ease-out forwards;
    transition: opacity .3s;
    
  }

  .anime-leave-active {
    animation: slide-out 0.3s ease-in forwards;
    transition: opacity 0.3s;
    opacity: 0;
  }

  @keyframes slide-in {
      from {
          transform: translateY(-30px);
      }
      to {
          transform: translateY(0);
      }
  }

  @keyframes slide-out {
      from {
          transform: translateY(0);
      }
      to {
          transform: translateY(-30px);
      }
  }
/* animation style end*/

</style>