<template>
  <v-app-bar
      :class="[currentRouteName === 'Landing' ? 'hideIt' : '']"
      class="white ct-pad"
      :height="getHeight"
      style='box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05);'
      app
      hide-on-scroll
      scroll-off-screen
      :scroll-threshold="200"
    >
        <div class="d-flex">
          <!-- left tags -->
          <div class='nev-margin'>
            <router-link to="/" style='text-decoration: none;' > 
              <v-img
                class="mr-5 pointer"
                src="../assets/new_files/logo.png"
                max-height="130"
                max-width="130"
                contain
              ></v-img>
              
            </router-link>
            <div class="para--text text-center" style="font-size: 0.7rem;">{{ mode }}</div>
          </div>

          <div class="search-tab ml-2">
            <div class="nev-margin hide-searchbar">
              <SearchBar :render="true" :getquery="true" :realtime="true" />
              <!-- <SeachBarc/> -->
            </div>
            
            <div class="subtab-cont hide-searchbar">
              <SubTabs />
            </div>          
          </div>        
        </div>

        <v-spacer></v-spacer>

        <!-- right tags -->
        <div class="right-tab-cont nev-margin">
          <!-- large screen right navigation tags -->
          <div class='hidefortablg'>
            <div class="d-flex align-top" style="transform: translateY(-2px)">
              <v-tooltip bottom nudge-top="-5" color="#EDF2FF" allow-overflow dark content-class="tooltip" >  
                <template v-slot:activator="{ on }">              
                  <img v-on="on" @click="changeHeadTab(0)"
                    :src="[0 === getMode ? job_active : job_inactive]"                   
                    class="right-navicons mr-3"
                  >
                </template>

                <span>
                  <div class="cl_tool"></div>
                  <div>Search Job</div>
                </span>
              </v-tooltip>

              <v-tooltip bottom nudge-top="-5" v-if="getCandidate.email"
                color="#EDF2FF" allow-overflow dark content-class="tooltip" >
                <template v-slot:activator="{ on }"> 
                  <v-badge
                    top
                    color="green accent-4"
                    dot
                    offset-x="10"
                    offset-y="10"
                  >
                    <v-avatar
                      style="cursor: pointer"
                      size="41"
                    >        
                    <img v-on="on" @click="changeHeadTab(1)"
                      :src="getProfileImage"
                    >
                    </v-avatar>
                  </v-badge>
                </template>

                <span>
                  <div class="cl_tool"></div>
                  <div>Account Settings</div>
                </span>
              </v-tooltip>
            </div>
          </div>   

          <!-- small screen right navigation tags -->
          <div class='hidefortabsm'>
            <div class="d-flex mb-1" @click="showSmallInput=!showSmallInput">
              <v-avatar
                size="40"
              >
              <img :src="[
                  getHeaderTabs[getMode] === 'search_job' ? 
                  job_active :
                  getProfileImage
                ]" class='right-navicons'  />
              </v-avatar>
               <!-- <img :src="dropimg" alt="dropdown" class="drop-style" /> -->
            </div> 
            
            <transition name='anime' type="animation">  

              <div style="position: absolute" class="d-flex flex-column mt-3" v-if="showSmallInput">
                <img class="search_tag-sm" @click="changeHeadTab(0)"
                  :class="[
                    0 !== getMode ? 'grow' : 'hideIt',
                    0 !== 1 ? 'mb-2' : ''
                  ]"
                  
                  @mousemove="hoverActive('search_job')"
                  @mouseleave="outDeactive('search_job')"

                  :src="[getMode === 0 ? job_active : job_inactive]" 
                />
                
                <v-avatar
                  size="40"
                >
                <img class="search_tag-sm" @click="changeHeadTab(1)"
                    :class="[
                      1 != getMode ? 'grow' : 'hideIt',
                      1 != 1 ? 'mb-2' : ''
                    ]"
                    
                    @mousemove="hoverActive('account_settings')"
                    @mouseleave="outDeactive('account_settings')"
                    :src="getProfileImage" 
                  />
                </v-avatar>
              </div>     

            </transition>  
          </div>   

        </div>
  </v-app-bar>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex'
  import router from '../router'

  import SearchBar from './SearchBar'
  // import SeachBarc from './SearchBarc'
  import SubTabs from './SubTabs'

  //icons imports
  import job_active from '../assets/new_file01/job_active.png'
  import job_inactive from '../assets/new_file01/job_inactive.png'
  import social_img from '../assets/social.png'
  // import seeker_active from '../assets/new_file01/seeker_active.png'
  // import seeker_inactive from '../assets/new_file01/seeker_inactive.png'
  // import alert_active from '../assets/new_file01/alert_active.png'
  // import alert_inactive from '../assets/new_file01/alert_inactive.png'

  // import account from '../assets/new_file01/account.png'
  import dropimg from '../assets/new_files/chevron-down.png'

  export default {
    name: 'AppBar',
    data: () => ({
      modeIndex: 0,
      job_active,
      job_inactive,
      currentIndexTab: 0,
      tabModes: {
        search_job: false,
        account_settings: false
      },
      showSmallInput: false,
      dropimg: dropimg,
      screenW: 1000,
      mode: 'Search Jobs'
    }),
    components: {
      // SeachBarc,
      SearchBar,
      SubTabs
    },
    methods: {
      ...mapActions(["updateMode"]),
      hoverActive(index) {
        this.tabModes[index] = true
      },
      outDeactive(index) {
        this.tabModes[index] = false
      },
      changeHeadTab(val) {
        this.$store.commit('setMode', val)
        this.showSmallInput = false
        this.$store.commit('setRouted', true)
        
        val == 0 ? router.push({ name: 'Live_results', query: this.$route.query }) : router.push({ name: 'Dashboard' })
        
      }
      // changeMode(index) {
      //   this.modeIndex = index
        
      //   if(index === 0) {
      //     this.updateMode(index)
      //     this.mode ="Search Jobs"
      //   } else if (index === 1) {
      //     this.updateMode(index)
      //     this.mode ="Search Jobseeker"
      //   }

      //   this.showSmallInput = false
      // }
    },
    computed: {
      ...mapGetters(['getMode', 'getHeaderTabs','getCandidate']),
      getHeight() {
        
        window.addEventListener('resize', () => { 
          this.screenW = window.innerWidth 
        })

        if (this.screenW > 998) {
          return 140
        } else {
          return 90
        }
      },
      getProfileImage() {
        if(this.getCandidate.avatar.url) {
          return this.getCandidate.avatar.url
        } else {
          return social_img
        }
      },
      currentRouteName() {
        return this.$route.name;
      }
    },
    mounted() {
      this.screenW = window.innerWidth
      this.currentRouteName === 'Dashboard' && this.$store.commit('setMode', 1)
      this.currentRouteName === 'Live_results' && this.$store.commit('setMode', 0)
    }
  }
</script>

<style scoped>
  .search-tab {
    position: relative;
  }

  .ct-pad {
    padding: 0 10px;
  }

  .subtab-cont {
    position: absolute;
    margin-top: 7px;
    z-index: -1000;
  }

  .nev-margin {
    transform: translateY(-1rem);
  }

  .right-navicons {
    height: 2.5rem;
    width: 2.5rem;
    cursor: pointer;
  }

  /* svg experment */
  .green {
    fill: yellowgreen;
  }

  .ct_tool {
    /* position: absolute; */
    width: 3.3rem;
    height: 3.3rem;
    background-color: red;
    /* transform: rotate(45deg); */
   
  }

  .tooltip {
    /* font-weight: 700; */
    color: #89AAFF;
    font-size: 0.8rem;
  }

  /* right nav small screen */
  .search_tag-sm {
    height: 2.4rem;
    width: 2.4rem;
    cursor: pointer;
  }

   .drop-style {
    height: 1.2rem;
    padding-left: 4px;
    opacity: 0.7;
    width: auto;
    transform: translateY(.6rem);
   }
  /* animation style */
.anime-enter {
    opacity: 0;
  }

.anime-enter-active {
    animation: slide-in 0.3s ease-out forwards;
    transition: opacity .3s;
    
  }

  .anime-leave-active {
    animation: slide-out 0.3s ease-in forwards;
    transition: opacity 0.3s;
    opacity: 0;
  }

  @keyframes slide-in {
      from {
          transform: translateY(-30px);
      }
      to {
          transform: translateY(0);
      }
  }

  @keyframes slide-out {
      from {
          transform: translateY(0);
      }
      to {
          transform: translateY(-30px);
      }
  }

   .grow { transition: all .2s ease-in-out; }
  .grow:hover { transform: scale(1.2) }
  .grow:active { transform: scale(0.9) }
/* animation style end*/

/* small screen config */

@media only screen and (max-width: 1160px) {
  .hidefortablg {
    display: none !important;
  }
}

@media only screen and (min-width: 1160px) {
  .hidefortabsm {
    display: none !important;
  }
}

@media only screen and (max-width: 998px) {
  .hide-searchbar {
    display: none !important;
  }

  .nev-margin {
    transform: translateY(0rem);
  }

  .ct-pad {
    padding: 0 4px;
  }

  .ct-height {
    height: 90px !important;
    padding: 2px 4px !important;
  }
}
</style>
