export default function(query) {
    
    let queryStr = {}

    if(query.title.trim().length !== 0) {
        queryStr.title = query.title.trim()
    } 

    if(query.location.trim().length !== 0) {
        queryStr.location = query.location.trim()
    }

    if(query.remote.trim().length !== 0) {
        queryStr.remote = query.remote.trim()
    }

    if(query.jobType.trim().length !== 0) {
        queryStr.jobType = query.jobType.trim()
    }

    if(query.salary.trim().length !== 0) {
        queryStr.salary = query.salary.trim()
    }

    if(query.skills.length !== 0) {
        queryStr.skills = query.skills
    }

    return queryStr
}