import axios from 'axios'
// import { v4 as uuidv4 } from 'uuid'
// import moment from 'moment'
import addIdResult from '../../utils/addIdResult'

const state = {
    candidate: {
        id: '',
        email: '',
        name: '',
        subscription: {
            admin: true, user: false, duration: 0
        },
        package: 'monthly',
        subValue: false,
        avatar: {
            url: '',
            name: 'default-img'
        }
    },
    
    routed: false
}

const getters = {
    getCandidate: (state) => {
        let { admin, user, duration } = state.candidate.subscription

        let subValue = admin && user && duration > Date.now()
        return { ...state.candidate, subValue: subValue }
    },
    getRouted: state => state.routed
}

const actions = {
    async fetchUserData({ commit, getters }) {
        commit('setLoading', true)
    
        try {
            const response = await axios.get(`/api/candidate`)

            commit('setResults', {
                live_result: getters.getResults.live_result,
                interested: addIdResult(response.data.data.jobs_section.interested, true),
                applied: addIdResult(response.data.data.jobs_section.applied, true),
                invited_offer: addIdResult(response.data.data.jobs_section.invited_offer, true)
            })

            delete response.data.data.jobs_section

            let resultData = response.data.data
            commit('setCandidate', resultData)
            
            // commit('setUser', { ...state.job_results, live_result: response.data })
            commit('setLoading', false)
            return true
        } catch (err) {
            console.log("Bulk fetch error: ", err)
            commit('setLoading', false)
            return false
        }
    }
}

const mutations = {
    setCandidate: (state, value) => state.candidate = value,
    setRouted: (state,value) => state.routed = value
}

export default {
    state,
    getters,
    actions,
    mutations
}