<script>
    import { mapGetters } from 'vuex'

    export default {
        computed: {
            ...mapGetters(['getResults', 'getCurTab'])
        },
        methods: {
            changeSubTab(data) {
                this.$store.commit('setCurSubTab', data)
            }
        },
        mounted() {
            let element = document.getElementById('vertical-tab')
            if(element) {
                element.remove()
            }
        }
    }
</script>

