<template>
  <v-app class="main-container">
    <v-progress-linear
      style="position: absolute; z-index: 100"
      indeterminate
      color="primary"
      :active="getLoading"
    ></v-progress-linear>
    <div v-if="!currentRouteName">
      <AppBar />
      <div class="sub-header-xs">
        <SearchBarXs :render="true" :getquery="true" :realtime="true" />
        <SubTabs /> 
      </div>
    </div>

    <v-main :class="[currentRouteName ? '' : 'routes-container', 'pt-0']">
      <transition name="slide">
      <router-view></router-view>
      </transition>
    </v-main> 
    
  </v-app>
</template>

<script>

import AppBar from './components/AppBar'
import SearchBarXs from './components/SearchBarXs'
import SubTabs from './components/SubTabs'
import { mapGetters, mapActions } from 'vuex'
import TokenService from './services/storage'
import setAuthToken from './services/setAuthToken'
import jwt_decode from 'jwt-decode'

export default {
  name: 'App',

  components: {
    AppBar,
    SearchBarXs,
    SubTabs
  },

  data: () => ({
    //
  }),

  computed: {
    ...mapGetters(['getLoading', 'getCandidate']),
    currentRouteName() {
      return this.$route.name === "Landing" 
        || this.$route.name === "Login" 
        || this.$route.name === "Dashboard"
        || this.$route.name === "Subscription"
        || this.$route.name === "PaymentPage"
    }
  },
  methods: {
    ...mapActions(['fetchUserData'])
  },
  mounted() {


    if (TokenService.getToken() && !this.getCandidate.email) {
      
      const decoded = jwt_decode(TokenService.getToken())

      // Check for expired token
      const currentTime = Date.now() / 1000
      if (decoded.exp > currentTime) {
        setAuthToken(TokenService.getToken())
        this.fetchUserData()
      } 
    } 
  }
};
</script>

