import { getField, updateField  } from 'vuex-map-fields'

const state = {
    mode: 0,
    headerTabs: ['search_job','account_setting']
}

const getters = {
    getMode: state => state.mode,
    getHeaderTabs: state => state.headerTabs,
    getField
}

const actions = {
    updateMode({ commit }, value) {
        commit('setMode', value)
    }
}

const mutations = {
    setMode: (state, value) => state.mode = value,
    updateField 
}

export default {
    state,
    getters,
    actions,
    mutations
}

