<template>
    <div class="d-flex justify-center align-center land-container">
        <div class="signin">
            
            <router-link to="/dashboard" style="text-decoration: none;" v-if="getCandidate.email">
                <span v-if="getCandidate.email" class="mt-4">
                    <v-badge
                        top
                        color="green accent-4"
                        dot
                        offset-x="10"
                        offset-y="10"
                    >
                        <v-avatar size="40" @click="$router.push({ name: 'Dashboard' })">
                            <v-img :src="getCandidate.avatar.url"></v-img>
                        </v-avatar>
                    </v-badge>
                </span>
            </router-link>

            <router-link to="/login" style="text-decoration: none;" v-else>
                <v-btn height="30"
                    elevation="0" dark  
                    color="white"    
                    style="border-radius: 6px; color: #0046FE; transform: translateX(8px)"
                    class="text-capitalize font-weight-bold ct-btn-border">
                <span class="d-flex align-center justify-center">

                    <span class="d-flex align-center" 
                        style="font-size: 0.8rem; text-transform: none !important;">
                        <div class="mr-1">Sign in</div>
                        <span>
                            <img style="height: 1rem; width: auto" class="mt-1"
                            src="../assets/new_files/user.png" alt="user">
                        </span>
                    </span>
                </span>
                </v-btn>
            </router-link>
        </div>
        <div class="search-panel">
            <div class="d-flex justify-center mb-6">
                <img src="../assets/new_files/logo.png" alt="app-logo" class="appLogo">
            </div>

            <div class="large-searchbar"><SearchBar /></div>
            <div class="small-searchbar"><SearchBarXs /></div>
            
        </div>
        
    </div>
</template>

<script>
    import SearchBarXs from '../components/SearchBarXs'
    import router from '../router'
    import { mapGetters } from 'vuex'

    export default {
        name: "Landing",
        data() {
            return {
                show: false
            }
        },
        components: {
            SearchBar: () => import('../components/SearchBar'),
            SearchBarXs
        },
        computed: {
            ...mapGetters(['getCandidate'])
        },
        beforeMount() {
            router.push({ query : {} }).catch(()=>{})
        }
    }
</script>

<style scoped>
    .land-container {
        height: 100vh;
    }

    .search-panel {
        transform: translateY(-32%);
    }

    .appLogo {
        margin: 0 auto;
        height: 2.2rem;
        width: auto;
    }

    .small-searchbar {
        display: none;
    }

    .signin {
        position: absolute;
        right: 0;
        top: 0;
        margin: 15px 15px;
    }

    @media only screen and (max-width: 960px) {
        .large-searchbar {
            display: none;
        }
        .small-searchbar {
            display: block;
        }
    }
</style>